<template>
  <div>
    <main> <!-- Début du main -->
        <form class="admin">
          <h1>Editer l'intro</h1>
          <editor v-model="intro.content"
          :options="defaultOptions"
          height="500px"
          previewStyle="vertical"
          mode="wysiwyg"
          ref="Editor"/>
          <input type="button" value="Enregistrer" @click="save()">
          <BarLoader v-show="loading" style="margin: 0 auto; border-radius:5px, display: none"></BarLoader>

        </form>
    </main><!-- fin main -->
  </div>
</template>
<script>
// @ is an alias to /src
import { mapActions, mapGetters } from 'vuex'
import { Editor } from '@toast-ui/vue-editor'


export default {
  name: 'home',
  components: {
    Editor
  },
  data () {
    return {
        intro: {},
        loading: false,
        defaultOptions: {
            language: 'fr_FR',
            useCommandShortcut: true,
            useDefaultHTMLSanitizer: true,
            usageStatistics: true,
            hideModeSwitch: false,
            toolbarItems: [
                'heading',
                'bold',
                'italic',
                'strike',
                'divider',
                'hr',
                'quote',
                'divider',
                'ul',
                'ol',
                'indent',
                'outdent',
                'divider',
                'table',
                'image',
                'link',
                'divider',
                'code',
                'codeblock'
            ]
        }
    }
  },
  // Fetches posts when the component is created.
  created () {
    window.scrollTo(0, 0)
    this.fetchIntro()
    .then(()=>{
        this.intro.content = this.getIntro.content
    })
  },
  computed: {
        ...mapGetters('jwt',[
        'jwt',
        'jwtSubject',
        'jwtIssuer'
        ]),
        ...mapGetters('intro',[
        'getIntro',
        'introLoading'
        ])
  },
  methods: {
    ...mapActions('intro',[
      'fetchIntro',
      'updateIntro'
    ]),
    ...mapActions('alert',[
      'newAlert'
    ]),
    getHtml() {
        return this.$refs.Editor.invoke('getMarkdown')
        // return this.$refs.Editor.invoke('getHtml') -> check github @toast-ui.vue-editor issues, if getHtml method works
    },
    save() {
        this.loading = true;
        this.updateIntro({
            content: this.getHtml()
            })
            .then(()=>{
                this.newAlert({type:'success', content:'Successfully edited'})
                this.$router.push('/')
            })
            .catch(e=>{
                this.newAlert({type:'error', content:e.response.data})
            })
    }
  }
}
</script>
